import React, {Component} from 'react';

import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import ToDoList from '../todo-list';
import ItemAddForm from '../item-add-form';

import './app.css';

export default class App extends Component {

    idCounter = 1;

    state = {
        doings: [
            this.createTodoItem('Drink coffee'),
            this.createTodoItem('Learn React'),
            this.createTodoItem('Skill upp'),
            this.createTodoItem('abc'),
            this.createTodoItem('abc'),
            this.createTodoItem('cba'),
        ],
        term: '',
        filter: 'All'
    };

    createTodoItem(label) {
        return {
            label: label,
            done: false,
            important: false,
            id: this.idCounter++
        };
    };

    deleteItem = (id) => {
        this.setState(({doings}) => {
            const idx = doings.findIndex((elem) => elem.id === id );

            const newDoinds = [
                ...doings.slice(0, idx),
                ...doings.slice(idx + 1)
            ];

            return {
                doings: newDoinds
            }
        });
    };

    addItem = (text) => {
        this.idCounter++;
        const newTestItem = this.createTodoItem(text);

        this.setState(({doings}) => {
            const newDoinds = [
                ...doings,
                newTestItem
            ];

            return {
                doings: newDoinds
            }
        })
    };

    toggleProperty(arr, id, propName) {
        const idx = arr.findIndex((elem) => elem.id === id );

        const oldItem = arr[idx];
        const newItems = {...oldItem, [propName]: !oldItem[propName]};

        return [
            ...arr.slice(0, idx),
            newItems,
            ...arr.slice(idx + 1)
        ];
    }

    onToggleDone = (id) => {
        this.setState(({doings}) => {
            return {
                doings: this.toggleProperty(doings, id, 'done')
            }
        });
    };

    onToggleImportant = (id) => {
        this.setState(({doings}) => {
            return {
                doings: this.toggleProperty(doings, id, 'important')
            }
        });
    };

    search(items, term) {
        if (term.length === 0) return items;

        return items.filter((elem) => {
            return elem.label
                .toLowerCase()
                .indexOf(term.toLowerCase()) > -1
        })
    }

    onSearchChange = (term) => {
        this.setState({term})
    };

    filter(items, filter) {

        switch (filter) {
            case 'All':
                return items;
            case 'Active':
                return items.filter((elem) => !elem.done );
            case 'Done':
                return items.filter((elem) => elem.done );
            default:
                return items;
        }
    };

    onFilterSorting = (filter) => {
        this.setState({filter})
    };

    render () {
        const {doings, term, filter} = this.state;

        const doneCount = doings.filter((elem) => elem.done).length;
        const toDoCount = doings.length - doneCount;

        const visibleItems = this.filter(this.search(doings, term), filter);

        return (
            <div className='wrapper'>
                <AppHeader toDo={toDoCount} done={doneCount} />
                <main>
                    <SearchPanel
                        onFilterSorting={this.onFilterSorting}
                        onSearchChange={this.onSearchChange}
                        filter={filter} />
                    <ToDoList
                        todos={visibleItems}
                        onDeleted={this.deleteItem}
                        onToggleDone={this.onToggleDone}
                        onToggleImportant={this.onToggleImportant} />
                    <ItemAddForm onAddItem={this.addItem} />
                </main>
            </div>
        )
    }
};